import { registerPlugin } from '@wordpress/plugins';
import { PluginSidebar } from '@wordpress/edit-post';
import { tool } from '@wordpress/icons';
import TabPanel from './components/tab-panel';
import { DataTab, SettingsTab, LibraryTab } from './components/tabs';
import { updateSettings } from './utils/settings';
import { DataTabProvider } from './context';

const PluginSidebarTest = () => {
	updateSettings();

	return (
		<PluginSidebar
			name="plugin-sidebar-test"
			title="Gutenbuild"
			icon={ tool }
		>
			<DataTabProvider>
				<TabPanel
					tabs={ [
						{
							name: 'data',
							title: 'Data',
							className: 'tab-one',
							content: <DataTab />,
						},
						{
							name: 'Libraries',
							title: 'Libraries',
							className: 'tab-two',
							content: <LibraryTab />,
						},
						{
							name: 'settings',
							title: 'Settings',
							className: 'tab-three',
							content: <SettingsTab />,
						},
					] }
				/>
			</DataTabProvider>
		</PluginSidebar>
	);
};

registerPlugin( 'plugin-sidebar-test', { render: PluginSidebarTest } );
